.left-sidebar{
    width: 164px;
    box-shadow: 1px 1px 0 rgba(0, 0, 0,0.2);
    transition: box-shadow ease-in-out 0.1s;
    transform: translateZ(0);
    box-sizing: border-box;
}
.side-nav{
    height: auto;
    max-width: 100%;
    position: sticky;
    margin: 50px 0px;
    padding: 20px 0px;
}
.side-nav-div{
padding: 10px 0px;
text-decoration: none;
/* display: flex;
align-items: center;
justify-content: flex-start; */
}
.active{
    font-weight: bolder;
    color: black;
    background-color: rgb(225,224,225);
    border-right: solid 3px #ef8236;
}
.side-nav-links:hover{
    color: black;
}
.side-nav-links{
    text-decoration: none;
    color: #3a3a3a;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    padding-left: 10px;
    transition: 0.2s;
}
.side-nav-div div{
    padding-left: 10px;
}